
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

export default {
name: "center",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
loading
},
data() {
return {
redmessageInfo: "", //个人中心数据
showLoding: false,
params: {
plat: 3
}
};
},
created() {
if (this.token) {
this.newredmessage();
}
},
methods: {
...mapActions([
"updateUser",
"updateCenter",
"updateToken",
"updateAccount"
]),
onCopy() {
this.$vux.toast.text("复制成功", "top");
},
onCopyErr() {
console.log("onCopyErr");
},

async newredmessage() {
let that = this;
this.showLoding = true;
let data = {
account: that.account,
token: that.token
};
data = Object.assign(this.params, data);
const [err, res] = await api.newredmessage(data);
if (err) {
that.showLoding = false;
that.$vux.toast.text(err.msg);
return;
}
this.showLoding = false;
this.redmessageInfo = res.data;
this.updateCenter(JSON.stringify(res.data));
},

async newgetorderlist_num_info() {
let data = {};
const [err, res] = api.newgetorderlist_num();
},

exitLogin() {
if (!this.user) {
this.$router.push("/user/login");
} else {
this.redmessageInfo=""
this.updateUser("");
this.updateToken("");
this.updateAccount("");
this.updateCenter("");
localStorage["user"] = "";
localStorage["account"] = "";
localStorage["token"] = "";
this.$vux.toast.text("退出成功");
}
}
},
computed: {
...mapGetters(["user", "account", "token"])
}
};



