
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
components: {
Tabbar,
TabbarItem
},
data() {
return {
index: 0
};
},
methods: {
setTabbarItem(path) {
if (path.indexOf("index") !== -1) {
this.index = 0;
} else if (path.indexOf("center") !== -1) {
this.index = 1;
} else {
this.index = 1;
}
},
_watchRouter(to) {
let path = to.path;
this.setTabbarItem(path);
}
},
created() {
let { path } = this.$router.history.current;
this.setTabbarItem(path);
},
watch: {
$route(to, from) {
this._watchRouter(to);
}
}
};



